import * as React from "react";
import teamImageRef from "../../assets/backdrop/team-image.jpg";
import "./TeamSection.scss";

export const TeamSection = () => {

    return (
        <div className="team-section">
            <div className="team-section-background" style={
                {
                    backgroundImage: `linear-gradient(
                        rgba(9, 7, 12, 1),
                        rgba(9, 7, 12, 0.8),
                        rgba(9, 7, 12, 0.5),
                        rgba(9, 7, 12, 0.25),
                        rgba(9, 7, 12, 0.5), 
                        rgba(9, 7, 12, 0.8), 
                        rgba(9, 7, 12, 1)
                      ),
                      url(${teamImageRef})`
                }
            }>
                <div className="team-section-content-wrapper flex-column">
                    <div className="team-section-content-title">
                        ABOUT US
                    </div>
                    <div className="team-section-content-caption">
                        We aim to integrate <span className="red-text">edifying narration</span> into <span className="red-text">innovative games</span>.
                    </div>
                    <div className="team-section-content-desc">
                        <span className="red-text">Rank B</span> is a start-up game developer team. We are passionate about bringing memorable immersive gaming experiences to indie game players all over the world.
                        Codename: <span className="red-text">Apocalypse</span> is our team's first project.
                    </div>
                    <div className="team-section-content-desc">
                        We believe it can provide a refreshing experience to players by new mechanics of <span className="red-text">contest point-based</span> card battles with stories in <span className="red-text">pre-apocalypse scenarios</span>.
                    </div>
                </div>
            </div>
        </div >
    );
}